/*
  バーガーメニュー
*/
.btn-burger {
  display: inline-block;
  position: relative;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  z-index: 100;
  cursor: pointer;
  transition: 0.5s;
}

.btn-burger:hover .btn-burger__icon {
  background: #405276;
}

.btn-burger:hover .btn-burger__icon:before, .btn-burger:hover .btn-burger__icon:after {
  background: #405276;
}

.btn-burger__icon {
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  width: 30px;
  height: 3px;
  margin: 0;
  background: #000;
  transition: .2s;
}

.btn-burger__icon:before, .btn-burger__icon:after {
  display: block;
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 30px;
  height: 3px;
  background: #000;
  transition: .3s;
}

.btn-burger__icon:before {
  margin-top: -13px;
}

.btn-burger__icon:after {
  margin-top: 10px;
}

.btn-burger__text {
  position: absolute;
  top: 50px;
  left: -8px;
  transform: rotate(90deg);
  font-family: 'Amiri',"ヒラギノ明朝 ProN W6", "HiraMinProN-W6", "HG明朝E", "ＭＳ Ｐ明朝", "MS PMincho", "MS 明朝";
  font-size: 18px;
  font-size: 1.125rem;
  letter-spacing: .04em;
}

.btn-burger__icon--close {
  background: transparent !important;
}

.btn-burger__icon--close:before, .btn-burger__icon--close:after {
  margin-top: 0;
}

.btn-burger__icon--close:before {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.btn-burger__icon--close:after {
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}

.btn-top__wrapper {
  padding: 28px 0 0;
}

.btn-top__wrapper.full {
  display: flex;
  justify-content: center;
  background: #d3d3da;
}

.btn-top {
  display: flex;
  align-items: center;
  justify-content: center;
  transform: rotate(45deg);
  width: 64px;
  height: 64px;
  border-top: 1px solid #fff;
  border-left: 1px solid #fff;
  cursor: pointer;
  transition: .2s;
}

